nav {
    background-color: rgb(215, 215, 215);
    padding: 0 5% 0 0;
}

.nav-wrapper {
    grid-area: nav;
}

#navL {
    font-size: 1.25em;
    color: forestgreen;
}

#logo {
    -webkit-transform: translateY(-30%);
            transform: translateY(-30%);
    max-height: 250px;
}

#hamburger {
    font-size: 2.5em;
    color: forestgreen;
}

#nav-mobile {
    -webkit-transform: translateY(35%);
            transform: translateY(35%);
    color: #608832;
}

.sidenav {
    opacity: 0.5;
}

.sidenav li>a {
    font-size: 1.5em;
    text-shadow: 3px 3px 10px #107C10;
}

img#sNavLogo.responsive-img {
    margin-top: 1.5%;
    margin-left: -5%;
}

@media only screen and (min-width: 768px) {
    nav {
        height: 100px;
    }
  }

@media only screen and (min-width: 360px) and (max-width: 767px) {
    nav {
        height: 75px;
    }

    #hamburger {
        font-size: 2em;
    }
}

@media only screen 
and (min-device-width : 360px) 
and (max-device-width : 812px) 
and (-webkit-device-pixel-ratio : 3)
and (orientation : landscape) {
    nav {
        height: 100px;
    }
}
#smIcon {
    max-width: 2em;
    color: forestgreen;
    margin: 1em;
}

.page-footer {
    background-color:rgb(215, 215, 215);
    border-top: 2px solid rgb(190, 188, 188);
    padding: 0;
    color: forestgreen;
}

.copyright {
    padding: 0 0 1em 0;
    font-size: .75em;
}

@media only screen and (min-width: 320px) and (max-width: 767px) {
    .icons {
        margin: 0 auto;
        text-align: center;
    }
}

@media only screen and (min-width: 768px) {
    .icons {
        margin-right: 10%;
    }
  }
#about {
    grid-area: about;

}

#team {
    margin: 0%;
}

#skyline {
    width: 90%;
    padding: 2.5%;
    margin: 0 5% 0 5%;
}

#intro {
    font-size: 1.5em;
    font-style: italic;
    text-align: center;
    margin-bottom: 5%;
}

.aboutUS {
    text-align: center;
}


@media screen and (max-width: 1030px) and (min-width: 320px) { 
    #intro {
        font-size: 1em;
    }

}

#criteria {
    grid-area: criteria;
    line-height: .5em;
}

#acquisition {
    font-size: 1.5em;
    text-align: center;
}

h1 {
    text-align: center;
}

@media screen and (max-width: 1030px) and (min-width: 320px) {
    #acquisition {
        font-size: 1em;
    }
}


#alexV,
#alexG {
    width: 350px;
    height: 200px;
    text-align: center;
    grid-area: vCards;
} 

#alexV {
    align-self: end;
}

#cardContainer {
    margin-bottom: 10%;
    grid-area: vCards;
}

h3 {
    font-size: 1.5em;
}

#vCardTitle {
    font-size: 2.0em;
    margin-bottom: 2.5%;
}

@media screen and (max-width: 1030px) and (min-width: 320px) {
    #vCardTitle {
        font-size: 1.5em;
        margin-bottom: 2.5%;
    }

    div#alexG.card {
        margin: 0 0 5px 0;
    }
}
#contact {
    grid-area: contact;
}

form {
    padding: 2em;
}

.card {
    margin: 0 2.5% 2.5% 2.5%;
}

title {
    margin: 0;
}
html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

body {
  font-family: 'Cormorant Garamond', serif;
  color: forestgreen;
  font-size: 18px;
}

.divider {
  margin: 1.5% 0;
}

#root {
    min-height: 100%;
    overflow-x: hidden;
    display: grid;
    grid-template:  "nav nav nav nav"
                    "about about about about"
                    "criteria criteria portfolio portfolio"
                    "criteria criteria portfolio portfolio"
                    "contact contact contact contact"
                    "vCards vCards vCards vCards"
                    "footer footer footer footer"; 
    grid-template-columns: repeat(4, 1fr);
}

footer {
    grid-area: footer;
    margin-top: auto;
}

header {
    grid-area: nav;
    width: 100%;
}

#pageImages {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 1030px) and (min-width: 320px) { 

  #root {
   display: inline;
   display: initial;
   height: 100vh;
   width: 100vw;
   font-size: 18px;
  }

  h1 { 
    font-size: 1.80em;
    margin: 1.5% 0;
  }

}
#portfolio {
    grid-area: portfolio;
    text-align: center;
    line-height: .5em;
}

h1 {
    text-align: center;
}

#fourtyTwoThird {
    max-width: 200px;
    max-height: 200px;
    border: solid black 2px;
}


